import React from "react";

export default class OraEmployeeListEntry extends React.Component {
    constructor() {
        super();
    }

    render() {
        return (
                <tr>
                    <td>
                        <label className="o-radio opl-radio">
                            <input type="radio" name={this.props.radioName} value={this.props.employee.sisId} 
                                   defaultChecked={this.props.checked === this.props.employee.sisId} 
                                   onChange={this.props.radioChanged} /> 
                            <span className="o-ci"/>
                        </label>
                    </td>
                    <td>{this.props.employee.sisId}</td>
                    <td>{this.props.employee.lastName}</td>
                    <td>{this.props.employee.firstName}</td>
                    <td>{this.props.employee.loginOtsa}</td>
                    <td>{this.props.employee.fullBscs}</td>
                    <td>{this.props.employee.location}</td>
                    <td>{this.props.employee.salesChannelName}</td>
                    <td>{this.props.employee.sisChannelDescription}</td>
                </tr>
        );
    } 
}

OraEmployeeListEntry.defaultProps = {
    employee: {},
    radioName: "selectedEmployee",
    radioChanged: function(event) {},
};
